import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Home',
        component: Home,
        children: [{
            path: '/home',
            component: () =>
                import ('../views/pages/Main.vue')
        }, {
            path: '/trade',
            component: () =>
                import ('../views/pages/Trade.vue')
        }, {
            path: '/block',
            component: () =>
                import ('../views/pages/Block.vue')
        }, {
            path: '/walletdetails',
            component: () =>
                import ('../views/pages/Walletdetails.vue')
        }, {
            path: '/blockdetails',
            component: () =>
                import ('../views/pages/Blockdetails.vue')
        }, {
            path: '/tradedetails',
            component: () =>
                import ('../views/pages/Tradedetails.vue')
        }, {
            path: '/node',
            component: () =>
                import ('../views/pages/Node.vue')
        }, {
            path: '/destory',
            component: () =>
                import ('../views/pages/Destory.vue')
        }],
        redirect: '/home'
    }

]

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes
})

export default router