<script>
import { getchain, searchwallet, searchblock } from "../../api";
export default {
  data() {
    return {
      openNav: false,
      opensearch: false,
      path: "",
      chianlis: [],
      inpu: "",
      navlable: false,
      inpumin: "",
    };
  },
  mounted() {
    this.ip =
      sessionStorage.getItem("IP") == null
        ? "https://ex-api.morley.cash"
        : sessionStorage.getItem("IP");
    this.path = this.$route.path;
    //获取侧链数量
    getchain()
      .then((res) => {
        this.chianlis = res.data.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  methods: {
    addClass() {
      this.openNav = !this.openNav;
    },
    opensearchtable() {
      this.opensearch = !this.opensearch;
    },
    routerclick(p) {
      this.$router.push(p);
      this.path = p;
    },
    routerclickmin(p) {
      this.$router.push(p);
      this.path = p;
      this.openNav = !this.openNav;
    },
    changeLinemain() {
      sessionStorage.setItem("IP", "https://ex-api.morley.cash");
      sessionStorage.setItem("unit", "MCN");
      sessionStorage.setItem("en", "Morley");
      location.reload();
    },
    changeLinemainmin() {
      sessionStorage.setItem("IP", "https://ex-api.morley.cash");
      sessionStorage.setItem("unit", "MCN");
      sessionStorage.setItem("en", "Morley");
      this.openNav = !this.openNav;
      location.reload();
    },
    changeLine(ip, symbol, en) {
      console.log(ip, symbol);
      this.ip = ip;
      sessionStorage.setItem("IP", ip);
      sessionStorage.setItem("unit", symbol);
      sessionStorage.setItem("en", en);
      location.reload();
    },
    searchInfo() {
      if (this.inpu !== "") {
        if (this.inpu.split("").length == 34) {
          window.scrollTo({
            top: 10,
            behavior: "smooth",
          });
          searchwallet({ address: this.inpu })
            .then((res) => {
              this.$router.push({
                path: "/walletdetails",
                query: {
                  id: res.data.data[0].address,
                },
              });
            })
            .catch((err) => {
              this.$alert("请输入正确钱包地址", "提示", {
                confirmButtonText: "确定",
              });
              this.inpu = "";
              window.console.log(err);
            });
        } else if (this.inpu.split("").length == 64) {
          this.$router.push({
            path: "/tradedetails",
            query: {
              id: this.inpu,
            },
          });
        } else if (
          this.inpu.split("").length == 6 ||
          this.inpu.split("").length == 5
        ) {
          this.$router.push({
            path: "/blockdetails",
            query: {
              id: this.inpu,
            },
          });
        } else if (
          this.inpu.split("").length == 20 ||
          this.inpu.split("").length == 19
        ) {
          searchblock({ id: this.inpu })
            .then((res) => {
              this.$router.push({
                path: "/blockdetails",
                query: {
                  id: res.data.data[0].height,
                },
              });
            })
            .catch((err) => {
              this.$alert("请输入正确钱包地址", "提示", {
                confirmButtonText: "确定",
              });
              this.inpu = "";
              window.console.log(err);
            });
        } else {
          this.$notify.error({
            title: "警告",
            message: "请输入正确的钱包地址/交易id/区块高度",
            type: "warning",
            duration: 4500,
          });
          this.inpu = "";
        }
      }
    },
    handleSubmit2() {
