          });
          this.inpu = "";
        }
      }
    },
    changeLineMin(ip, symbol, en) {
      this.ip = ip;
      sessionStorage.setItem("IP", ip);
      sessionStorage.setItem("unit", symbol);
      sessionStorage.setItem("en", en);
      location.reload();
    },
    openlable() {
      this.navlable = !this.navlable;
    },
    tomedia(t) {
      if (t == "t") {
        window.open("https://twitter.com/morley10528184");
      } else if (t == "f") {
        window.open("https://www.facebook.com/morleyglobal/");
      } else {
        window.open("https://t.me/morley2020");
      }
    },
    towebsite() {
      location.href = "https://morley.cash";
    },
  },
};
