import axios from 'axios'
import Vue from 'vue'
// import qs from 'qs'
// var ip = 'https://ex-api-domainss.ycsmbcs.com'
let getIP = sessionStorage.getItem('IP')
if (getIP) {
    var ip = getIP
} else {
    ip = 'https://ex-api.morley.cash'
}
Vue.prototype.$ip = ip
export function getwallet(params) { return axios.get(ip + '/api/v2/wallets/' + params) } //钱包
export function getwalletbalance(params) { return axios.get(ip + `/api/v2/wallets/${params}`) } //钱包余额和公匙
export function getwalletrecord(params) { return axios.get(ip + `/api/v2/wallets/${params}/transactions`) } //钱包记录
export function getblockslist(params) { return axios.get(ip + '/api/v2/blocks', { params }) } //区块列表
export function getblockdetail(params) { return axios.get(ip + `/api/v2/blocks`, { params }) } //区块详情
export function getblockdetailheight(params) { return axios.get(ip + `/api/v2/blocks/${params}`) } //区块详情高度
export function getdeallist(params) { return axios.get(ip + '/api/v2/transactions', { params }) } //交易
export function getdealdetail(n, p, l) { return axios.get(ip + `/api/v2/wallets/${n}/transactions?orderBy=timestamp:desc&page=${ p }&limit=${l}`) } //交易详情
export function getdelegates(params) { return axios.get(ip + '/api/v2/delegates', { params }) } //节点
export function getblockhgeight(params) { return axios.get(ip + '/api/v2/blockchain', { params }) } //获取区块高度供应量
export function getdeal(params) { return axios.get(ip + '/api/v2/blockchain', { params }) } //获取区块高度供应量
export function getnodedetail(params) { return axios.get(ip + `/api/v2/delegates/${params}`) } //节点
export function getvotes(params) { return axios.get(ip + `/api/v2/delegates/${params}/voters`) } //选民

// 请求单个钱包全部交易
