//请求单个交易内容
export function getdealone(params) { return axios.get(ip + '/api/v2/transactions/' + params) }

//请求交易类型
export function getdealtype() { return axios.get(ip + '/api/v2/transactions/types') }

//请求交易列表
export function getdealall(params) { return axios.get(ip + '/api/v2/transactions', { params }) }

//请求交易的总条数
export function getdealT() { return axios.get(ip + '/api/v2/transactions') }

//请求区块列表
export function getblockAll() { return axios.get(ip + '/api/v2/blocks') }

//请求所有钱包
export function getwalletAll(params) { return axios.get(ip + '/api/v2/wallets/top', { params }) }

//请求8个钱包
export function getwalletE(params) { return axios.get(ip + '/api/v2/wallets', { params }) }


//请求整个区块的供应
export function getblockApply() { return axios.get(ip + '/api/v2/blockchain') }


//获取区块的交易列表
export function getblocktradeList(params) { return axios.get(ip + '/api/v2/blocks/' + params + '/transactions') }

